<template>
  <a-modal
    v-model:open="open"
    :title="title"
    @cancel="handleCancel"
    :footer="null"
    :maskClosable="false"
  >
    <a-form
      ref="formRef"
      :label-col="{ span: 6 }"
      :wrapper-col="{ span: 16 }"
      autocomplete="off"
      :model="formState"
      :rules="rules"
      @finish="onFinish"
      @finishFailed="onFinishFailed"
      style="margin: 10%"
    >
      <a-form-item label="Êñá‰ª∂Âà´Âêç" name="name">
        <a-input v-model:value="formState.name" />
      </a-form-item>

      <a-form-item v-if="!formState.id" label="Êñá‰ª∂‰∏ä‰º†" name="files">
        <a-upload
          v-model:file-list="fileList"
          name="file"
          :headers="headers"
          :customRequest="uploadRequest"
          :before-upload="beforeUploadFj"
          @change="changeFj"
          @remove="removeFj"
          :maxCount="1"
        >
          <a-button> ‰∏ä‰º† </a-button>
        </a-upload>
      </a-form-item>

      <div>
        <a-form-item :wrapper-col="{ offset: 10, span: 16 }">
          <a-button type="primary" html-type="submit">Êèê‰∫§</a-button>
          <a-button style="margin: 0 10px" @click="handleCancel" type="default"
            >ÈÄÄÂá∫</a-button
          >
          <a-button @click="resetForm" type="dashed">ÈáçÁΩÆ</a-button>
        </a-form-item>
      </div>
    </a-form>
  </a-modal>
</template>

<script setup>
import { ref, reactive, defineExpose, defineEmits, unref } from "vue";
// import dayjs from "dayjs";
import axios from "axios";
import { message } from "ant-design-vue";
import { Upload } from "ant-design-vue";

const emit = defineEmits(["close"]);
const open = ref(false);

function validateFiles(rule, value, callback) {
  console.log("üöÄ ~ validateFiles ~ rule:", rule);
  console.log("üöÄ ~ validateFiles ~ value:", value);
  if (fileList.value.length <= 0) {
    callback(new Error("ËØ∑‰∏ä‰º†Êñá‰ª∂!"));
  } else {
    callback();
  }
}
const rules = {
  name: [{ required: true, message: "Êñá‰ª∂ÂêçÁß∞!" }],
  files: [{ required: true, validator: validateFiles, trigger: "change" }],
};
const formRef = ref();
const title = ref(undefined);

// const formData = reactive({ name: "" });

// ÂêéÁ´ØÂú∞ÂùÄ
const apiUrl = "https://definedh163.github.io:3000"; // ÊõøÊç¢‰∏∫‰Ω†ÁöÑÂêéÁ´Ø API Âú∞ÂùÄ

// const actionUrl = `${apiUrl}/uploads`;
const formState = reactive({
  name: "",
  id: undefined,
  files: undefined,
});

const fileState = reactive({
  id: undefined,
  fileName: undefined,
  downloadUrl: undefined,
});

const fileList = ref([]);
// const uploadUrl = "http://localhost:3000/upload-file"; // ÂêéÁ´ØÊñá‰ª∂‰∏ä‰º†Êé•Âè£
const headers = {
  "Content-Type": "multipart/form-data",
};

const onShow = (id = undefined) => {
  open.value = true;
  title.value = `${id ? "ÁºñËæë" : "Êñ∞Â¢û"}Êñá‰ª∂`;
  if (id) {
    formState.id = id;
    getInfo(id);
  }
};

function resetToUndefined(obj) {
  Object.keys(obj).forEach((key) => {
    obj[key] = undefined;
  });
}

const handleCancel = () => {
  resetForm();
  open.value = false;
};

const getInfo = (id) => {
  // Ê†πÊçÆid Ëé∑ÂèñË°®ÂçïÊï∞ÊçÆ
  const url = `${apiUrl}/getTableInfoById/${id}`;
  axios.get(url).then((res) => {
    let { code, row } = res.data;
    if (code == 200) {
      formState.name = row.name;
      formState.files = JSON.parse(row.files);
    }
  });
};

const onFinish = (values) => {
  console.log("Success:", values);
  //updateTableData
  const url = `${apiUrl}/${formState.id ? "updateTableData" : "postFormData"}`; // get Êü•ÁúãÊï∞ÊçÆÂ∫ìÊï∞ÊçÆ

  formState.files = Object.assign({}, fileState);
  if (formState.id) {
    axios.put(url, unref(formState)).then((res) => {
      let { code, msg } = res.data;
      if (code == 200) {
        message.success(msg);
        emit("close");
        resetForm();
        handleCancel();
      }
    });
  } else {
    axios.post(url, formState).then((res) => {
      let { code, msg } = res.data;
      if (code == 200) {
        message.success(msg);
        emit("close");
        resetForm();
        handleCancel();
      }
    });
  }
};
const onFinishFailed = (errorInfo) => {
  console.log("Failed:", errorInfo);
};

const resetForm = () => {
  formRef.value.resetFields();
  resetToUndefined(fileState);

  fileList.value.pop();
};

function formatFileSize(sizeInBytes) {
  const sizeInGB = sizeInBytes / (1024 * 1024 * 1024);
  return `${sizeInGB.toFixed(2)}gb`;
}

const uploadRequest = (event) => {
  const formData = new FormData();
  formData.append("file", event.file);
  formData.append("name", event.file.name); // Ê∑ªÂä†ÁºñÁ†ÅÂêéÁöÑÊñá‰ª∂ÂêçÂà∞ FormData

  const url = `${apiUrl}/uploads`;
  axios
    .post(url, formData, {
      headers: {
        "Content-Type": "multipart/form-data; charset=utf-8", // ËÆæÁΩÆËØ∑Ê±ÇÂ§¥‰∏∫ multipart/form-dataÔºåÂπ∂ÊåáÂÆöÂ≠óÁ¨¶ÈõÜ‰∏∫ UTF-8
      },
    })
    .then((res) => {
      let { code } = res.data;
      if (code == 200) {
        event.onSuccess(res, event.file); //‰∏ä‰º†ÊàêÂäüÁõëÂê¨‰∫ã‰ª∂
        fileState.id = res.data.data.id;
        fileState.fileName = res.data.data.name;
        fileState.downloadUrl = res.data.data.downloadUrl;
      }
    })
    .catch((err) => {
      event.onError(err, err, event.file);
    });
};

const changeFj = (info) => {
  if (info.file.status !== "uploading") {
    message.success(`${info.file.name} ‰∏ä‰º†‰∏≠!`);
  }
  if (info.file.status === "done") {
    fileList.value = info.fileList;
    message.success(`${info.file.name} ‰∏ä‰º†ÊàêÂäü!`);
  } else if (info.file.status === "error") {
    message.success(`${info.file.name} ‰∏ä‰º†Â§±Ë¥•!`);
    fileList.value.pop();
  }
};

const removeFj = (file) => {
  console.log("üöÄ ~ removeFj ~ file:", file);
  fileList.value.pop();
  resetToUndefined(fileState);
};

const beforeUploadFj = (file) => {
  console.log("üöÄ ~ beforeUploadFj ~ file:", file);
  const isAcceptType = [
    "application/msword",
    "application/vnd.openxmlformats-officedocument.wordprocessingml.document",
    "application/vnd.ms-excel",
    "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet",
    "application/vnd.ms-powerpoint",
    "application/pdf",
    "application/vnd.openxmlformats-officedocument.presentationml.presentation",
    "application/x-zip-compressed",
    "image/tiff",
    "application/vnd.android.package-archive",
  ];
  if (!isAcceptType.includes(file.type)) {
    message.error("‰ªÖÊîØÊåÅdoc„ÄÅxls„ÄÅppt„ÄÅpdf„ÄÅdocx„ÄÅxlsx„ÄÅpptx„ÄÅzip„ÄÅtiff„ÄÅapk");
    return false || Upload.LIST_IGNORE;
  }
  const isLt10M = file.size / 1024 / 1024 < 500;
  fileState.fileSize = formatFileSize(file.size);
  if (!isLt10M) {
    message.error("‰∏ä‰º†Êñá‰ª∂Â§ßÂ∞è‰∏çËÉΩË∂ÖËøá 500MB!");
  }
  return isLt10M || Upload.LIST_IGNORE;
};

defineExpose({ onShow });
</script>
